import store from './display'

function addProduct(e) {
    let a = e.target.value ;
    let r;

    if(a === 'arch')
        r =  {
        type : 'ARCH'
        };
    if(a === 'bacon')
        r= {
            type : 'BACON'
        };
    if(a === 'basil')
        r =  {
            type : 'BASIL'
        };
    if(a === 'chili')
        r =  {
            type : 'CHILI'
        };
    if(a === 'mozzarella')
        r =   {
            type : 'MOZZARELLA'
        };
    if(a === 'mushroom')
        r =   {
            type : 'MUSHROOM'
        };
    if(a === 'olive')
        r =   {
            type : 'OLIVE'
        };
    if(a === 'onion')
        r =   {
            type : 'ONION'
        };
    if(a === 'pepper')
        r =   {
            type : 'PEPPER'
        };
    if(a === 'pepperoni')
        r =   {
            type : 'PEPPERONI'
        };
    if(a === 'tomato')
        r =   {
            type : 'TOMATO'
        };
    if(a === 'all')
        r =   {
            type : 'ALL'
        };
    if(a === 'disAll')
        r =   {
            type : 'DIS'
        };
    return r;


}
function addRecepies(e) {
    let a = e.target.className.slice(0,e.target.className.indexOf(' '));
    let r ;
    console.log(a);
    if(a === 'basilFeta')
        r =  {
            type : 'BASILANDFETA'
        };
    if(a === 'chiliR')
        r= {
            type : 'CHILIR'
        };
    if(a === 'doubleMeet')
        r =  {
            type : 'DOUBLE'
        };
    if(a === 'fresh')
        r =  {
            type : 'FRESH'
        };
    if(a === 'karbonara')
        r =   {
            type : 'KARBONARA'
        };
    if(a === 'vegetable')
        r =   {
            type : 'VEGETABLE'
        };
    if(a === 'pepperoni')
        r =   {
            type : 'PEP'
        };
    if(a === 'dietary')
        r =   {
            type : 'DIET'
        };
    store.dispatch(r);
}

export {addProduct,addRecepies}


