import React from 'react';
import arch from "../ready/anchovy.png"
import bacon from "../ready/bacon.png"
import basil from "../ready/basil.png"
import chili from "../ready/chili.png"
import mozzarella from "../ready/mozzarella.png"
import mushroom from "../ready/mushroom.png"
import olive from "../ready/olive.png"
import onion from "../ready/onion.png"
import pepper from "../ready/pepper.png"
import pepperoni from "../ready/pepperoni.png"
import tomato from "../ready/tomato.png"
import {connect} from 'react-redux';
import {addProduct} from "../reducer/actions";


 class Ingridients extends React.Component{
    render(){
        return(
            <div className='boardBack'>
                <div className="board">
                    <div className="base">
                        <img src={arch} alt="arch" className={this.props.store.arch ? "display ingredients" : "notDisplay ingredients" } />
                        <img src={bacon} alt="bacon" className={this.props.store.bacon ? "display ingredients" : "notDisplay ingredients"}  />
                        <img src={pepperoni} alt="pepperoni" className={this.props.store.pepperoni ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={basil} alt="basil"  className={this.props.store.basil ? "display ingredients" : "notDisplay ingredients"}/>
                        <img src={chili} alt="chili" className={this.props.store.chili ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={mushroom} alt="mushroom" className={this.props.store.mushroom ? "display ingredients" : "notDisplay ingredients"}  />
                        <img src={olive} alt="olive" className={this.props.store.olive ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={onion} alt="onion" className={this.props.store.onion ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={pepper} alt="pepper" className={this.props.store.pepper ? "display ingredients" : "notDisplay ingredients"}  />
                        <img src={tomato} alt="tomato" className={this.props.store.tomato ? "display ingredients" : "notDisplay ingredients"} />
                        <img src={mozzarella} alt="mozzarella" className={this.props.store.mozzarella ? "display ingredients" : "notDisplay ingredients"}/>
                    </div>
                </div>
            </div>
        )
    }
}
export default connect(
    state => ({
        store : state
    }),
    dispatch => ({
        add : (e) => dispatch(addProduct(e))
    })
)(Ingridients)


