import { createStore } from 'redux'
import { applyMiddleware } from 'redux'
import thunk from 'redux-thunk';

const beginState ={
    arch : false,
    bacon : false,
    basil : false,
    chili : false,
    mozzarella : false,
    mushroom : false,
    olive : false,
    onion : false,
    pepper : false,
    pepperoni : false,
    tomato : false,
    sum : 5
};

const mainState =(state= beginState,action) =>{
    switch (action.type){
        case 'ARCH':
            return {
                ...state,
                arch : !state.arch
            };
        case 'BACON':
            return {
                ...state,
                bacon : !state.bacon
            };
        case 'BASIL':
            return {
                ...state,
                basil : !state.basil
            };
        case 'CHILI':
            return {
                ...state,
                chili : !state.chili
            };
        case 'MOZZARELLA':
            return {
                ...state,
                mozzarella : !state.mozzarella
            };
        case 'MUSHROOM':
            return {
                ...state,
                mushroom : !state.mushroom
            };
        case 'OLIVE':
            return {
                ...state,
                olive : !state.olive
            };
        case 'ONION':
            return {
                ...state,
                onion : !state.onion
            };
        case 'PEPPER':
            return {
                ...state,
                pepper : !state.pepper
            };
        case 'PEPPERONI':
            return {
                ...state,
                pepperoni : !state.pepperoni
            };
        case 'TOMATO':
            return {
                ...state,
                tomato : !state.tomato
            };
        case 'ALL':
            return {
                arch: true,
                bacon: true,
                basil: true,
                chili: true,
                mozzarella: true,
                mushroom: true,
                olive: true,
                onion: true,
                pepper: true,
                pepperoni: true,
                tomato: true
            };
        case 'BASILANDFETA':
            return {
                tomato : true,
                basil: true,
                mozzarella: true,
                mushroom: true,
                olive: true,
                pepper: true,
                arch : false,
                bacon : false,
                chili : false,
                onion : false,
                pepperoni : false

            };
        case 'CHILIR':
            return {
                chili : true,
                basil: true,
                mozzarella: true,
                mushroom: true,
                olive: true,
                pepper: true,
                bacon: true,
                arch : false,
                onion : false,
                pepperoni : false,
                tomato : false
            };
        case 'DOUBLE':
            return {
                pepperoni : true,
                basil: true,
                mozzarella: true,
                olive: true,
                pepper: true,
                bacon: true,
                arch : false,
                chili : false,
                mushroom : false,
                onion : false,
                tomato : false
            };
        case 'FRESH':
            return {
                basil: true,
                arch: true,
                tomato: true,
                olive: true,
                pepper: true,
                bacon : false,
                chili : false,
                mozzarella : false,
                mushroom : false,
                onion : false,
                pepperoni : false
            };
        case 'KARBONARA':
            return {
                mozzarella: true,
                mushroom: true,
                tomato: true,
                bacon: true,
                arch : false,
                basil : false,
                chili : false,
                olive : false,
                onion : false,
                pepper : false,
                pepperoni : false
            };
        case 'VEGETABLE':
            return {
                tomato : true,
                basil: true,
                mozzarella: true,
                mushroom: true,
                olive: true,
                pepper: true,
                onion: true,
                arch : false,
                bacon : false,
                chili : false,
                pepperoni : false
            };
        case 'PEP':
            return {
                mozzarella: !true,
                mushroom: true,
                tomato: true,
                pepperoni: true,
                arch : false,
                bacon : false,
                basil : false,
                chili : false,
                olive : false,
                onion : false,
                pepper : false
            };
        case 'DIET':
            return {
                chili : true,
                tomato : true,
                basil: true,
                mozzarella: true,
                mushroom: true,
                olive: true,
                pepper: true,
                onion: true,
                arch : false,
                bacon : false,
                pepperoni : false
            };
        case 'COUNT':
            return {
                ...state,
                sum : action.sum
            };
        case 'DIS':
            return beginState;
        default: return state;
    }
};

const store = createStore(mainState,applyMiddleware(thunk));
export default store


