import React from 'react';
import {addProduct} from '../reducer/actions'
import {connect} from 'react-redux';
class Tabs extends React.Component{
    render(){
        return(
            <div className='tabs'>
                <section>
                    <button value='arch' onClick={this.props.add} className={this.props.store.arch ? "active" : "notActive"}>Anchovies</button>
                    <button value='bacon' onClick={this.props.add} className={this.props.store.bacon ? "active" : "notActive"}>Bacon</button>
                    <button value='basil' onClick={this.props.add} className={this.props.store.basil ? "active" : "notActive"}>Basil</button>
                    <button value='chili' onClick={this.props.add} className={this.props.store.chili ? "active" : "notActive"}>Chili</button>
                </section>
                <section>
                    <button value='mozzarella' onClick={this.props.add} className={this.props.store.mozzarella ? "active" : "notActive"}>Mozzarella</button>
                    <button value='mushroom' onClick={this.props.add} className={this.props.store.mushroom ? "active" : "notActive"}>Mushroom</button>
                    <button value='olive' onClick={this.props.add} className={this.props.store.olive ? "active" : "notActive"}>Olive</button>
                    <button value='onion' onClick={this.props.add} className={this.props.store.onion ? "active" : "notActive"}>Onion</button>
                </section>
                <section>
                    <button value='pepper' onClick={this.props.add} className={this.props.store.pepper ? "active" : "notActive"}>Pepper</button>
                    <button value='pepperoni' onClick={this.props.add} className={this.props.store.pepperoni ? "active" : "notActive"}>Pepperoni</button>
                    <button value='tomato' onClick={this.props.add} className={this.props.store.tomato ? "active" : "notActive"}>Tomato</button>
                    <button value='disAll' onClick={this.props.add}>DisAll</button>
                </section>


            </div>
        )
    }
}

export default connect(
    state => ({
        store : state
    }),
    dispatch => ({
        add : (e) => dispatch(addProduct(e))
    })
)(Tabs)


