import React from 'react';
import {addRecepies} from '../reducer/actions'
import basilAndFeta from "../recepies/basilAndFetaR.png"
import chiliR from "../recepies/chiliR.png"
import doubleMeetR from "../recepies/doubleMeetR.png"
import freshR from "../recepies/freshR.png"
import karbonaraR from "../recepies/karbonaraR.png"
import ovoshnayaR from "../recepies/ovoshnayaR.png"
import pepperoniR from "../recepies/pepperoniR.png"
import veganR from "../recepies/veganR.png"
import {connect} from 'react-redux';

 class Recepies extends React.Component{
    render(){
        return(
            <section className='readyRecipes'>
                <div className='recipes1'>
                    <figure onClick={addRecepies}>
                        <img src={basilAndFeta} className='basilFeta recipes' alt='a'/>
                        <figcaption>Basil and Feta</figcaption>
                    </figure>
                    <figure onClick={addRecepies}>
                        <img src={chiliR} className='chiliR recipes' alt='a'/>
                        <figcaption> Chili </figcaption>
                    </figure>
                    <figure onClick={addRecepies}>
                        <img src={doubleMeetR} className='doubleMeet recipes' alt='a'/>
                        <figcaption>Double Meet</figcaption>
                    </figure>
                    <figure onClick={addRecepies}>
                        <img src={freshR} className='fresh recipes' alt='a'/>
                        <figcaption>Fresh</figcaption>
                    </figure>
                </div>
                <div className='recipes2'>
                    <figure onClick={addRecepies}>
                        <img src={karbonaraR} className='karbonara recipes' alt='a'/>
                        <figcaption>Karbonara</figcaption>
                    </figure>
                    <figure onClick={addRecepies}>
                        <img src={ovoshnayaR} className='vegetable recipes' alt='a'/>
                        <figcaption>Vegetable</figcaption>
                    </figure>
                    <figure onClick={addRecepies}>
                        <img src={pepperoniR} className='pepperoni recipes' alt='a'/>
                        <figcaption>Pepperoni</figcaption>
                    </figure>
                    <figure onClick={addRecepies}>
                        <img src={veganR} className='dietary recipes' alt='a'/>
                        <figcaption>Dietary</figcaption>
                    </figure>
                </div>
            </section>
        )
    }
}
export default connect(
    state => ({
        store : state
    }),
    dispatch => ({
        add : (e) => dispatch(addRecepies(e))
    })
)(Recepies)


