import store from './display'
const begin ={
    arch : 3,
    bacon : 3,
    basil : 2,
    chili : 1,
    mozzarella : 3,
    mushroom : 2,
    olive : 1,
    onion : 1,
    pepper : 1,
    pepperoni : 3,
    tomato: 1,
    summary : 5
};
const mas = ['arch','bacon','basil','chili','mozzarella','mushroom','olive','onion','pepper','pepperoni','tomato'];
export default function count1() {

    for(let i = 0; i < mas.length; i++){
    if(store.getState()[mas[i]] === true){
        begin.summary = begin.summary + begin[mas[i]];
    }
    }
store.dispatch({
    type : 'COUNT',
    sum : begin.summary
});
    begin.summary = 5;
    console.log(store.getState().sum);
}



