import React from 'react';
import ReactDOM from 'react-dom';
import store from './reducer/display'
import Ingridients from './components/ingridients'
import Recepies from './components/recepies'
import Tabs from './components/tabs'
import count1 from "./reducer/sum";
import {Provider} from 'react-redux';

import Geolocation from "react-geolocation";
import { compose, withProps,withHandlers,lifecycle } from "recompose"
let a ,b,e;
class App extends React.Component{
    constructor(props){
        super(props);
        this.state={
            ok : false,
            lat : '',
            lon : '',
            pl : ''
        };
        this.validate = this.validate.bind(this);
        this.tet = this.tet.bind(this);
        this.tet1 = this.tet1.bind(this);
        this.tet2 = this.tet2.bind(this);
    }
    componentDidMount(){

    }
    tet(){
        console.log('tet')
        this.tet1();
        this.setState({
            lat : a
        })
    }
    tet1(){
        console.log('tet1')
        this.setState({
           lon : b
        })
    }
    tet2(){
        fetch(`https://maps.googleapis.com/maps/api/geocode/json?latlng=${this.state.lat},${this.state.lon}&key=AIzaSyANkyLK-kIz5HS56RyfJ9A6lleXQ3Lixdk`).then(response => {
            if (response.status >= 200 && response.status < 400) {
                return response.json()
            }
        }).then(res => this.setState({pl : (res.results[0].formatted_address)}));
    }
    validate(email) {
        let reg =  /^\w+@\w+\.\w{2,4}$/i;
        let address = email.target.value;
        if(reg.test(address) === false) {

            this.setState({
                ok: false
            });
        }
        else this.setState({
            ok: true
        });
    }
    render(){
        return(
            <Provider store={store}>
            <div className="mainPart">
                <Geolocation
                    onSuccess={console.log}
                    render={({
                                 fetchingPosition,
                                 position: { coords: { latitude, longitude } = {} } = {},
                                 error,
                                 getCurrentPosition,

                             }) =>
                        <div className='notDisplay'>
                            {a = latitude}
                            {b = longitude}
                        </div>}
                />
            <Ingridients />
                <div className='infoPart' onLoad={this.tet}>
                    <h1>Welcome to Pizza Creator!</h1>
                    <div className='registration' >
                        <div>
                    <label>Name</label>
                    <input type='text'/>
                        </div>
                        <br/>
                        <div>
                    <label>Email</label>
                    <input type='text' onChange={this.validate} className={this.state.ok ? 'ok' : 'again'}/>
                        </div>
                    <br/>
                    </div>
                    <div onClick={count1}>
                    <Recepies />
                    <Tabs />
                    </div>
                    <div className='totalCount' >
                        <span>Total count: </span>
                    <p>{store.getState().sum}$</p>
                    </div>
                    <p >You location is {this.state.place}:</p>
                    <input type='text' id='lon' onClick={this.tet2} onTouchStart={this.tet2} value={this.state.pl}/>
                </div>
            </div>
            </Provider>
        )
    }
}

const render = () => {
    ReactDOM.render(<App/>, document.getElementById('root'));
};
store.subscribe(render);
render();



